.container{
    position: relative;
    border-radius: .375rem;
    background-color:#fff;
    box-shadow: 0 4px 7px lightgray;
    overflow: hidden;
    text-align: center;
}

.img{
    display: inline-block;
    margin-top: 10px;
    box-shadow: 0 10px 10px lightgray;
}

.contents{
    font-size: 1rem;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    text-align: center;
    margin-top: 10px;
}

.btn{
    font-size: 1.125rem;
    text-align: center;
    float: right;
    margin: 10px 10px 10px 10px;
}

.link{
    text-decoration: none;
    color: black;
    display:"block"
}