body{
    background-color: #eff3f7;
}

.wrapper{
    margin: 0 auto;
    max-width: 1024px;
    padding: 150px;
}

.container{
    overflow: hidden;
}

.ul{
    overflow: hidden;
    list-style: none;
}
.li{
    box-sizing: border-box;
    float: left;
    padding: 0 10px 40px;
    width: 30%;
}

.li:nth-child(3n+1){
    clear: both;
}

.title{
    height: 1.625rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}